import { Component, OnInit, ViewChild, EventEmitter, ElementRef, Renderer2 } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { PartParoduct } from '../../_models/PartParoduct';
import { Processor } from '../../_models/Processor';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { AlertifyService } from '../../_services/alertify.service';
import { RequirementService } from '../../_services/requirement.service';
import { RequirementData } from '../../_models/RequirementData';
import { Deals } from '../../_models/Deals';
import { Offer } from '../../_models/Offer';
import { Select2OptionData } from 'ng-select2';
import { SpectDetail } from '../../_models/SpectDetail';
import { MyLocation } from '../../_models/MyLocation';
import { ChatBox } from '../../_models/ChatBox';
import { Generation } from '../../_models/Generation';
import { Subject } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';

@Component({
  selector: 'app-requirements',
  templateUrl: './requirements.component.html',
  styleUrls: ['./requirements.component.css']
})
export class RequirementsComponent implements OnInit {
  chatmodel: any = {};
  model: any = {};
  deals: any = {};
  chats: ChatBox[];
  roleid: any =  Number(String(localStorage.getItem('RoleID')));
  userid: any =  Number(localStorage.getItem('UserId'));
  LoginUser: any =  localStorage.getItem('LoginUser');
  makes: PartParoduct[];
  generations: Generation[];
  models: PartParoduct[];
  locations: MyLocation[];
  hdds: SpectDetail[];
  rams: SpectDetail[];
  processors: Processor[];
  requirements: RequirementData[];
  requirement: RequirementData[];
  deals1: Deals[];
  offers: Offer[];
  mydeals: Offer[];
  data = [];
  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[0, 'desc']]
};
  dtInstance: DataTables.Api;
  isDtInitialized: boolean = false;
  dtTrigger: Subject<RequirementData> = new Subject();
  @ViewChild('infoModal') public infoModal: ModalDirective;
  @ViewChild('stockModal') public stockModal: ModalDirective;
  @ViewChild('detailModal') public detailModal: ModalDirective;
  @ViewChild('ChatModal') public ChatModal: ModalDirective;
  @ViewChild('table') table: ElementRef;
  public exampleData: Array<Select2OptionData>;

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService,
    private requirementservice: RequirementService, private renderer: Renderer2) { }

  ngOnInit(): void {
    this.loadMakes();
    this.loadGenerations();
    this.loadLocations();
    this.loadRAMs();
    this.loadHDDs();
    this.loadProcessors();
    this.loadRequirements();
  }
  loadLocations() {
    return this.loadspecsService.loadLocations().subscribe((locations: MyLocation[]) => {
      this.locations = locations.filter(proj => (proj.status === 1));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadMakes() {
    return this.loadspecsService.getMakes().subscribe((partproducts: PartParoduct[]) => {
      this.makes = partproducts.filter(proj => (proj.type === 1));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadGenerations() {
    return this.loadspecsService.getGenerations().subscribe((generations: Generation[]) => {
      this.generations = generations;
    }, error => {
      this.alertify.error(error);
    });
  }
  loadModels(value: number) {
    return this.loadspecsService.getModels().subscribe((partproducts: PartParoduct[]) => {
      this.models = partproducts.filter(proj => (Number(proj.parentId) === Number(value)));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadRAMs() {
    return this.loadspecsService.loadSpectDetails().subscribe((rams: SpectDetail[]) => {
      this.rams = rams.filter(proj => (proj.parentId === 1 ));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadHDDs() {
    return this.loadspecsService.loadSpectDetails().subscribe((hdds: SpectDetail[]) => {
      this.hdds = hdds.filter(proj => (proj.parentId === 2));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadProcessors() {
    return this.loadspecsService.getProcessors().subscribe((processors: Processor[]) => {
      this.processors = processors;
    }, error => {
      this.alertify.error(error);
    });
  }
  loadRequirements() {
    return this.requirementservice.getRequirements().subscribe((requirements: RequirementData[]) => {
      this.requirements = requirements.filter(proj => (Number(proj.type) === 0) && Number(proj.flag) === 0);
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });

  }
  OpenstockModal(id: any) {
    return this.requirementservice.getRequirements().subscribe((requirement: RequirementData[]) => {
      this.requirement = requirement.filter(proj => (proj.requestID === id));
      this.model.ReqNo1 = this.requirement[0].requestID;
      this.model.MakeId1 = this.requirement[0].makeName;
      this.model.ModelId1 = this.requirement[0].modelName;
      this.model.ProcessorId1 = this.requirement[0].processor;
      return this.requirementservice.getOffers().subscribe((offers: Offer[]) => {
        this.mydeals = offers.filter(proj => (proj.requestID === id && proj.status === 0
          && proj.empId === Number(localStorage.getItem('UserId'))));
          this.stockModal.show();
      }, error => {
        this.alertify.error(error);
      });
    }, error => {
      this.alertify.error(error);
    });
  }
  openChatModal(id: number) {
    this.model.Ref_Id = id;
    this.chatmodel.ref_id = id;
    this.chatmodel.user_id = this.userid;
    return this.requirementservice.viewChats(this.chatmodel).subscribe((chats: ChatBox[]) => {
      this.chats = chats.filter(proj => (Number(proj.status) === 0 && proj.ref_Id === id));
      this.loadRequirements();
      this.ChatModal.show();
  }, error => {
    this.alertify.error(error);
  });
  }
  sendMessagePost() {
    this.model.U_Id = Number(localStorage.getItem('UserId'));
    if (!this.model.Text) {
      this.alertify.error('Enter Comment');
      return;
    }
    document.getElementById('SendSMS').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
    (document.getElementById('SendSMS') as HTMLButtonElement).disabled = true;
    this.requirementservice.sendChatPost(this.model).subscribe((chats: ChatBox[]) => {
      this.chats = chats.filter(proj => (Number(proj.status) === 0 && proj.ref_Id === this.model.Ref_Id));
      this.loadRequirements();
      this.model.Text = '';
      document.getElementById('SendSMS').innerHTML = 'Send';
      (document.getElementById('SendSMS') as HTMLButtonElement).disabled = false;
    }, error => {
      this.alertify.error(error);
    });
  }
  sendDealsPost() {
    this.deals.EmpId = localStorage.getItem('UserId');
    if (this.model.RamType === null) {
      this.alertify.error('Select Ram Type');
      return;
    }
    if (this.model.Ram === null) {
      this.alertify.error('Select RAM');
      return;
    }
    if (this.model.Hddtype === null) {
      this.alertify.error('Select HDD Type');
      return;
    }
    if (this.model.Hdd === null) {
      this.alertify.error('Select HDD');
      return;
    }
    if (this.model.Adapter === undefined) {
      this.alertify.error('Select Adapter');
      return;
    }
    if (!this.model.Qty1) {
      this.alertify.error('Enter Qty');
      return;
    }
    if (!this.model.price1) {
      this.alertify.error('Enter price');
      return;
    }
    if (!this.model.Comment1) {
      this.alertify.error('Enter Comment');
      return;
    }

    document.getElementById('AddmyDeal').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
    (document.getElementById('AddmyDeal') as HTMLButtonElement).disabled = true;

    this.deals.RequestId = this.model.ReqNo1;
    this.deals.Qty = this.model.Qty1;
    this.deals.Price = this.model.price1;
    this.deals.Comment = this.model.Comment1;
    this.deals.Hddtype = this.model.Hddtype;
    this.deals.Hdd = this.model.Hdd;
    this.deals.RamType = this.model.RamType;
    this.deals.Ram = this.model.Ram;
    this.deals.Adapter = this.model.Adapter;
    this.requirementservice.sendDealsPost(this.deals).subscribe((offers: Offer[]) => {
      this.mydeals = offers.filter(proj => (proj.requestID === this.model.ReqNo1 && proj.status === 0
        && proj.empId === Number(localStorage.getItem('UserId'))));
      this.model.Qty1 = '';
      this.model.price1 = '';
      this.model.Comment1 = '';
       document.getElementById('AddmyDeal').innerHTML = 'Add';
      (document.getElementById('AddmyDeal') as HTMLButtonElement).disabled = false;
      this.alertify.success('Deals Posted Successfull');
    }, error => {
      this.alertify.error(error);
    });
  }

  OpendetailModal(id: any) {
    return this.requirementservice.getOffers().subscribe((offers: Offer[]) => {
        this.offers = offers.filter(proj => (proj.requestID === id && proj.status === 0));
      this.detailModal.show();
    }, error => {
      this.alertify.error(error);
    });
  }
  ApproveDeals(Id: number,  value: string) {
    this.model = {Id : Id, Status: 1, Comment: value, app_by: Number(localStorage.getItem('UserId'))};
    document.getElementById('Dealsoffer_' + Id).innerHTML = '<span style="font-weight:bold;color:green;font-style:italic;font-size:15px">Approved</span>';
    this.requirementservice.ApproveRejectDealDeals(this.model).subscribe(() => {
      this.loadRequirements();
      this.alertify.success('Deal Approved');
    }, error => {
      this.alertify.error(error);
    });
  }
  Remove(Id: number) {
    this.alertify.confirm('are you sure ?', () => {
      this.deals.Id = Id;
      this.requirementservice.RemoveDealsPost (this.deals).subscribe((offers: Offer[]) => {
      this.mydeals = offers.filter(proj => (proj.requestID === this.model.ReqNo1 && proj.status === 0
        && proj.empId === Number(localStorage.getItem('UserId'))));
        this.loadRequirements();
      this.alertify.success('Deals Removed Successfull');
    }, error => {
     this.alertify.error(error);
    });
    });
  }
  RejectDeals(Id: number, value: string) {
    this.model = {Id : Id, Status: 2, Comment: value, app_by: Number(localStorage.getItem('UserId'))};
    document.getElementById('Dealsoffer_' + Id).innerHTML = '<span style="font-weight:bold;color:red;font-style:italic;font-size:15px">Rejected</span>';
    this.requirementservice.ApproveRejectDealDeals(this.model).subscribe(() => {
      this.loadRequirements();
      this.alertify.error('Deal Reject');
    }, error => {
      this.alertify.error(error);
    });
  }
  sendRequirementPost() {
    this.model.status = 0;
    this.model['userId'] = localStorage.getItem('UserId');
    if (this.model.MakeId === null) {
      this.alertify.error('Select Make');
      return;
    }
    if (this.model.ModelId  === null) {
      this.alertify.error('Select Model');
      return;
    }
    if (this.model.ProcessorId  === null) {
      this.alertify.error('Select Processor');
      return;
    }
    if (this.model.Ram === null) {
      this.alertify.error('Select RAM');
      return;
    }
    if (this.model.Hdd === null) {
      this.alertify.error('Select HDD');
      return;
    }
    if (this.model.gen_id === null) {
      this.alertify.error('Select Generation');
      return;
    }
    if (!this.model.Cname) {
      this.alertify.error('Enter Customer Name');
      return;
    }
    if (!this.model.Qty) {
      this.alertify.error('Enter Qty');
      return;
    }
    if (!this.model.req_type) {
      this.alertify.error('Select Type');
      return;
    }
    if (!this.model.Adapter) {
      this.alertify.error('Select Adapter');
      return;
    }
    // if (this.model.loc_id === null) {
    //   this.alertify.error('Select Location');
    //   return;
    // }
    document.getElementById('postingrequest').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
    (document.getElementById('postingrequest') as HTMLButtonElement).disabled = true;
    this.requirementservice.sendRequirementPost(this.model).subscribe((requirements: RequirementData[]) => {
      this.requirements = requirements.filter(proj => (Number(proj.type) === 0) && Number(proj.flag) === 0);
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
      this.model.MakeId = '0';
      this.model.ModelId = '0';
      this.model.ProcessorId = '0';
      this.model.Ram = '0';
      this.model.Hdd = '0';
      this.model.req_type = '0';
      this.model.Qty = '';
      this.model.Cname = '';
      document.getElementById('postingrequest').innerHTML = 'Submit';
      (document.getElementById('postingrequest') as HTMLButtonElement).disabled = false;
      this.infoModal.hide();
      this.alertify.success('Request Sent Successfull');
    }, error => {
      this.alertify.error(error);
    });
  }
  CloseRequirement(Id: any) {
    this.model = {RequestID : Id, Status: 1};
    this.requirementservice.CloseRequirement(this.model).subscribe(() => {
      document.getElementById('Req_' + Id).innerHTML = '<span style="font-weight:bold;color:green;font-style:italic;font-size:15px">Closed</span>';
      this.alertify.success('Requirement Closed');
    }, error => {
      this.alertify.error(error);
    });
  }

}
